<template>
  <el-breadcrumb class="app-breadcrumb" separator="/">
    <transition-group name="breadcrumb">
      <!--<el-breadcrumb-item v-for="(item,index)  in levelList" :key="item.path" v-if="item.name">-->
        <!--<span v-if="index==levelList.length-1" class="no-redirect">{{item.name}}</span>-->
        <!--<router-link v-else :to="item.redirect||item.path">{{item.name}}</router-link>-->
      <!--</el-breadcrumb-item>-->
      <el-breadcrumb-item v-for="(item,index)  in levelList" :key="item.path" v-if="item.meta.title">
        <span v-if="item.path === ''">{{item.meta.title}}</span>
        <router-link v-else :to="item.redirect||item.path">{{item.meta.title}}</router-link>
      </el-breadcrumb-item>
    </transition-group>
  </el-breadcrumb>
</template>

<script>
export default {
  created() {
    this.getBreadcrumb()
  },
  data() {
    return {
      levelList: null
    }
  },
  watch: {
    $route() {
      this.getBreadcrumb()
    }
  },
  methods: {
    getBreadcrumb() {
      const matched = this.$route.matched.filter(item => item.meta.title)
      //      const first = matched[0]
      //      if (first && first.name !== 'dashboard') {
      //        matched = [{ path: '/dashboard', meta: { title: '首页' }}].concat(matched)
      //      }
      this.levelList = matched
      console.log('levelList', this.levelList)
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .app-breadcrumb.el-breadcrumb {
    display: inline-block;
    font-size: 14px;
    line-height: 60px;
    .no-redirect {
      color: #fff;
      cursor: text;
    }
  }
</style>



